@media screen and (max-width: 1200px) {
    .slider-text h2{font-size: 50px;padding: .2% 0 0 19%;}
    .slider-text p{font-size: 1rem;}
    
}
@media screen and (max-width: 992px) {
    .navbar-toggler{padding: .25rem 0 .25rem .75rem;}
    .nav-link{border-bottom: 1px solid #ccc;}
    .navbar-nav .nav-item a{color: #aaa;text-align: center;}
    .navbar-nav .nav-item a:hover{color: #007bff;background: #f7f7f7;}
    .navbar-nav .nav-item .active{border-bottom: 2px #052b51 solid;color: #052b51;font-weight: bold;}
    .navbar .navbar-toggler .navbar-toggler-icon{width: 1em;height: 1em;};
    .slider-text h2{font-size: 40px; padding: 0 0 0 19%;margin-bottom: 0;}
    .slider-text p{font-size: .9rem;}
    .our-services > div > div > div:first-child{margin-bottom: 6%;}
    .our-services img{width: 25%; margin: 0 1% 3% 0;}
    .client-list-main{margin: 2% 0 0;}
    .counter h2 { font-size: 41px; font-style: italic; line-height: 4rem; }
    .counter ul { padding: 1rem 0 0 0; }
    .counter p{margin-bottom: 0;}
    .testimonials .testi-bg-box{height: 350px;}
    footer .social i {font-size: 32px;}
    
    /* New navbar starts */
    .overlay { height: 100%; width: 0; position: fixed; z-index: 999; top: 0; left: 0; background-color: rgb(12,77,144); background-color: rgba(12,77,144, 1); overflow-x: hidden; transition: 0.5s;  }
    .overlay-content { position: relative; top: 25%; width: 50%; text-align: center; margin: 30px auto;  }
    .overlay .closebtn { position: absolute; top: 20px; right: 45px; font-size: 60px;display: block;color: #fff;  }
    .overlay .closebtn:hover{text-decoration: none;}
    /* New navbar starts */
}
@media screen and (max-width: 800px) {
    .navbar-toggler{padding: .25rem 0 .25rem .75rem;}
    .slider-text h2{font-size: 30px; padding: 0 0 0 19%;margin-bottom: 0;}
    .slider-text p{font-size: 0.8rem;}
}

@media screen and (max-width: 768px) {
    .head-top{justify-content: center;text-align: center;}
    .head-top div:nth-of-type(2){width: 60%;}
    .slider-text h2{font-size: 30px; padding: 0 0 0 20%;margin-bottom: 0;}
    .slider-text p{font-size: 0.8rem; padding: 0 0 0 20%;}
    .slider-text{width: 90%;height: 32%;left: 4%;top: 58%;bottom: 1%;}
    .slider-text{left: 4%;}
    .carousel-indicators{display: flex;left: 0;bottom: 0;margin: 0 15%;top: unset;}
    .carousel-indicators li{width: 30px; height: 5px;}
    .our-services > div > div > div{margin-bottom: 6%;}
    .our-services > div > div > div:last-child{margin-bottom:0;}
    .our-services img{width: 15%; margin: 0 1% 1% 0;}
    .counter h2 {line-height: 2.8rem; }
    .counter li{ margin-right: 2rem;}
    .counter p{margin-top: .7rem;}
    .testimonials .testi-bg-box{height: 250px;}
    .testimonials .content:nth-of-type(2){margin-top: 3rem;}
    .map-container{height: 300px;}
    .contact-box-inner{padding: 1rem 7rem;}
    .contact-box p{padding-bottom: 0; font-size: 1rem;margin-bottom: 0;}
    .contact-box p a{font-size: 1rem;}

    
}

@media screen and (max-width: 700px) {
    .slider-text h2{font-size: 20px; padding: 0 0 0 20%;margin-bottom: 0;}
    .slider-text p{font-size: 0.8rem; padding: 0 0 0 20%;}
    .footericon{display: block; text-align: center;  width: 100%; background: #1fb2dc; color: white; font-size: 16px; font-weight: bold; text-decoration: none;cursor: pointer;}
    .footerit {display: none; overflow: hidden;border-right: 5px solid #1fb2dc; border-left: 5px solid #1fb2dc;  padding: 1rem;}
    .navbar-brand img{width: 50%;}
    .head-top div:nth-of-type(2){width: 80%;}
    section {padding: 2rem 0;}
    h3{font-size: 30px;padding-bottom: 2rem;}
    .our-services > div > div > div{margin-bottom: 10%!important;}
    .client-list ul li img{width: 50%;}
    .counter img{width: 100%;}
    .counter h2 {font-size: 30px; line-height: 2rem; }
    .counter ul{width: 70%; margin: 0 auto;}
    .counter ul li{width: 50%;margin: 0;    text-align: center;}
    .contact-box-inner {padding: 1rem 1rem;}
    h4{font-size: 30px;}
    footer {padding: 1rem 0;}
    .footericon{padding: 3px;}
    #myBtn{right: 10px;bottom: 60px;}
    section p{font-size: 1rem;line-height: 1.8rem;}
    .our-services a{font-size: 1rem;}
    .footer-ttl{text-align: left;}
    footer li{width: 100%;text-align: left;}
    .carousel-item .img-sm{display: block!important;}
    .carousel-item .img-big{display: none!important;}
    .slider-text{background-image: none; top: 76%;}
    .slider-text h2{padding: 0;}
    .slider-text p{padding: 0;}
    .head-top a{padding: 0;}
    .our-expertise img{padding-bottom: 1rem;}
}

@media screen and (max-width: 520px) {
    .slider-text{top: 70%;}
  }

@media screen and (max-height: 450px) {
    .overlay .closebtn {font-size: 40px; top: 15px; right: 35px;}
  }