p {font-size: 16px;}
li{list-style: none;}

section {padding: 4rem 0;overflow: hidden;}
footer {padding: 4rem 0;}
section p{font-family: 'Comfortaa', cursive;font-size:1.1rem; line-height: 2rem; color: #7e7e7e;}
.mb-1{margin-bottom: 1rem;}
.gray_bg{background: #f4f5f7;}

header{background: #0c4d90; padding: 10px;}
.head-top{justify-content: flex-end;}
.head-top a{font-size: 14px; color: white; padding: 0 0 0 20px;text-decoration: none;}
.head-top a:hover{text-decoration: underline; color: white;}
.dropdown-menu{padding: 0!important;}
.dropdown-item{padding: .25rem .5rem!important; color: #212529!important;border-bottom: 1px solid #ccc;}

/* Top icons css start */
.dropdown-toggle::before{content: "";background-image: url(/images/eng-icon.jpg);display: inline-block;width: 26px; height: 16px;    margin: 0 6px 2px 0;  vertical-align: middle;}
