/* Search icon animation css start */
.search-contain{position: relative;}
.searchBox{color: #fff;}
.search-colr{color: #FFF; position: absolute; right: 15px; top: 4px; }
input[type=text] { width: 30px; background: #6d94bc; border-radius: 3px; border: 1px solid #fff; padding: 0 5px; display: block; height: 22px; font-size: 14px; color: #fff; margin: 0 0 0 10px;transition: all 0.5s ease;}
input[type=text]:focus { width: 150px;}
/* Search icon animation css start */




h3 {font-size: 42px; color: #1fb2dc; font-family: 'Poppins', sans-serif; font-weight: 300; padding-bottom: 3rem; margin-bottom: 0;}
h3::after{content: " "; border-bottom: solid #1fb2dc 3px; width: 33px; display: block; margin-top: -5px;}
h4 {font-size: 42px; color: #333333; font-family: 'Poppins', sans-serif; font-weight: 300; padding-bottom: 2rem; margin-bottom: 0;}
h4::after{content: " "; border-bottom: solid #333333 3px; width: 33px; display: block; margin-top: -5px;}
