.navbar{border-bottom: 4px solid #1fb2dc;}
.navbar-nav .nav-item a{color: #7e7e7e; font-size: 16px;}
.navbar-nav .nav-item a:hover{color:#104d96;background: #f7f7f7;}
.navbar-nav .nav-item .active{border-bottom: 2px #000 solid;color: #000;}
.navbar-toggler-icon{color: #7e7e7e; font-size: 22px;display: contents;}
.navbar-brand img{width: 80%;}

.overlay .closebtn { position: absolute; top: 20px; right: 45px; font-size: 60px;display: none;  }
  

.carousel .carousel-indicators {position: absolute; right: 0; left: unset; top: 68%;  z-index: 15; display: -ms-flexbox;  display: table-column; -ms-flex-pack: center; justify-content: center; padding-left: 0; margin-right: 7%; margin-left:unset; list-style: none;}
.carousel .carousel-indicators li{width: 7px; height: 20%; border-top:0;}
.slider-text{position: absolute;top: 63%; left: 7%; color: #fff; width: 84%; height: 29%; font-family: 'Poppins', sans-serif;background-image: url(/images/slider/slider-icon.png);background-repeat: no-repeat;background-size: cover;}

.slider-text h2{font-style: italic; padding: 1.8% 0 0 19%;}
.slider-text p {font-style: italic;font-size: 1.2rem; text-shadow: 2px 2px 1px #000;padding-left: 19%;}
.carousel-item .img-sm{display: none!important;}
.big_banner{width: 100%;}


/*** Our Services css start **/
.our-services{background: #f4f5f7;}
.our-services h2{font-size: 18px;  width: 60%; display: inline-block; font-weight: bold; color: #1fb2dc; text-shadow: none;vertical-align: middle;}
.our-services img{vertical-align: middle; border-style: none; display: inline-block; width: 37%; margin: 0 1% 6% 0;}
.our-services a{font-size: 16px; color: #1fb2dc; text-decoration: underline;}
.our-services a:hover{color: #1fb2dc;text-decoration: none;}
.our-expertise h2{font-size: 1.5rem;margin-bottom: 1rem;color: #7e7e7e;text-shadow: none;font-weight: bold;}
.our-expertise img{width: 100%;}

.our-expertise ul li{padding:0 0 1rem 0;}
