/*** Our Services css ends **/


/*** client list css start **/
.client-list-main{margin: 12% 0;}
.client-list ul{text-align: center;}
.client-list ul li{float: left;width: 33.33%;}
.carousel-control-next, .carousel-control-prev{width: 3%;z-index: 999;}
.carousel-control-prev-icon{background-image:url(/images/client-list-prev.png) !important; background-repeat: no-repeat!important;}
.carousel-control-next-icon{background-image:url(/images/client-list-next.png) !important; background-repeat: no-repeat!important;}
/*** client list css ends **/


/*** Counter section css start **/
.counter{background-image: url(/images/counter.jpg);background-repeat: no-repeat;background-size: cover;height: vh-25; }
.counter ul{padding: 3rem 0 0 0;}
.counter li{float: left;margin-right:4rem;}
.counter li h2{color:#FFF; margin-bottom: -15px;}
.counter li p{color:#FFF!important;}
.counter h2{font-size: 55px;font-style: italic;line-height: 4rem;}
.counter .yellow{color: #fffc00;}
.counter .blue{color: #0ae4e1;}
/*** client list css ends **/

/*** Testimonials css start **/

/*.testimonials ul li{width: 45%; display: inline-block;margin: 0 2%;position: relative;}
.testimonials .content{position: relative;}*/
.testimonials img{width: 20%;margin: 0 20px 0 20px;}
.testimonials p{display: inline-block;width: 62%;vertical-align: middle;}
.testimonials .comma{font-size: 110px; color: #dedede;  display: block; font-family: 'Poppins', sans-serif;  height: 3rem; position: relative; top: -32px; left: 7rem; z-index: 9;}
.testimonials .testi-name{display: block;text-align:right ; font-size: 16px;color: #3a3a3a;width: 86%;}
.testimonials .testi-content{z-index: 999;position: relative;}
.testimonials .testi-bg-clip{clip-path: polygon(23% 8%, 91% 13%, 99% 40%, 87% 75%, 22% 84%, 5% 49%);position: absolute; top: 0; width: 40%; }
.testimonials .testi-bg-box{background-color:#f6f6f6;width: 100%;height: 250px; display: block;}
/*** Testimonials css ends **/


/*** Contact Us css ends **/
.map-container{height: 100%;}
.contact-box{background-color: #dddddd;}
.contact-box-inner{padding: 3rem;}
.contact-box p{color: #555555; font-size: 16px;line-height: 25px; padding-bottom: 1rem;}
.contact-box p a{color: #555555; font-size: 16px;line-height: 25px;text-decoration: none;}
.contact-box p a:hover{color:#000;}

/* footer css starts */
.footer-ttl{font-size: 16px; color: #1fb2dc; border-bottom: solid 1px #1fb2dc;text-transform: uppercase;}
footer li{font-size: 15px; color: #7e7e7e; display: inline-block;width: 175px;}
footer li a{color: #7e7e7e;text-decoration: none;}
footer li a:hover{text-decoration: none;}
footer .social i{font-size: 30px;}
footer .social a{ color: #7e7e7e;text-decoration: none;margin: 0 3px;}
footer .social a:hover{text-decoration: none;}

.footer-copyright{background-color: #1fb2dc; font-size: 1rem;color: #FFF;}
.footer-copyright a{color: #FFF; text-decoration: none;}
.footer-copyright a:hover{color: #FFF; text-decoration: none;}

.page-footer > a{text-decoration: none;cursor: pointer; color: white;}
.accordion button{border: none; width: 100%;margin-bottom: 1rem;outline: none;}
.accordion button a{text-decoration: none;color: white;}
.footericon {display: block; text-align: center;  width: 100%; background: #1fb2dc; font-size: 16px; font-weight: bold;   }
.footerit{border-right: none; border-left: none;  padding: 0 0 2rem 0;display: block;overflow: hidden;}
